<template>
	<div class="live-keep-live2keep-all">
        <mt-cell @click.native="showSelectorPaymentAccount()" title="网通活期账号：" placeholder="" :value="account || '请选择网通活期账号'" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
            <submit-button @submit="submit" :submitStatus="submitStatus" value="活期转整存整取"></submit-button>
		</div>
        <div class="field-warn">
            <ol>温馨提示：
                <li>1、整存整取50元起存。</li>
                <li>2、开户成功后，您可以在交易明细中查询定期账户。</li>
                <li>3、如有疑问，请致电96669。</li>
            </ol>
        </div>
	</div>
</template>

<script>

import * as cservice from '@/services/public';

export default{
	data() {
		return {
			account: '',
            submitStatus: false,
			selectorPaymentAccount: {
                title: '选择理财账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: []
            }
		};
	},
    watch: {
        account: function(val) {
            if(val){
                this.submitStatus = true;
            }else{
                this.submitStatus = false;
            }
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.account = data.item.ACCOUNT;
            }, data => {
            });
        },
        submit() {
        	this.$router.push({name: 'LiveKeepLive2KeepAllDetail', params: {account: this.account}});
        },
        back() {
        	history.back();
        },
        getCommonAccountList() {
            cservice.getCommonAccountList({
                flagType: '2'
            })
            .then((data) => {
                this.selectorPaymentAccount.accountList = data.LIST;
            }, (err) => {
                console.log(err);
            });
        }
	},
	created() {
        this.getCommonAccountList();
		this.account = this.account || this.$store.state.live2keepAll.account;
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateLive2keepAll', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateLive2keepAll', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.live-keep-live2keep-all{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


