<template>
	<div class="live-keep-keepSearch">
        <mt-cell @click.native="showSelectorPaymentAccount()" title="网通活期账号：" placeholder="" v-model="account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
            <submit-button @submit="submit" :submitStatus="submitStatus" value="定期账户查询"></submit-button>
		</div>
	</div>
</template>

<script>

import * as cservice from '@/services/public';
import service from '@/services/bank/live-keep';

export default{
	data() {
		return {
			account: '',
            submitStatus: false,
			selectorPaymentAccount: {
                title: '选择网通活期账号',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: []
            }
		};
	},
    watch: {
        account: function(val) {
            if(val){
                this.submitStatus = true;
            }else{
                this.submitStatus = false;
            }
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.account = data.item.ACCOUNT;
            }, data => {
            });
        },
        submit() {
        	this.getKeepSearchListForAccount(this.account);
        },
        back() {
        	history.back();
        },
        getCommonAccountList() {
            cservice.getCommonAccountList({
                flagType: '2'
            })
            .then((data) => {
                this.selectorPaymentAccount.accountList = data.LIST;
            }, (err) => {
                console.log(err);
            });
        },
        getKeepSearchListForAccount(account) {
            service.getKeepSearchListForAccount({
                ACCOUNT: account
            }).then((data) => {
                this.$router.push({name: 'LiveKeepKeepSearchList', params: {detail: data}});
            }, (err) => {
                this.$msg('当前活期账号下尚未有定期账户！');
            });
        }
	},
	created() {
        this.getCommonAccountList();
		this.account = this.account || this.$store.state.keepSearch.account;
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateKeepSearch', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateKeepSearch', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.live-keep-keepSearch{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


