<template>
	<div class="live-keep-live2keep-live-detail">
		<mt-cell title="活期账户信息"></mt-cell>
		<mt-cell title="活期账号：" :value="account.number"></mt-cell>
		<mt-cell title="可用余额：" :value="account.money"></mt-cell>
		<mt-cell title="币种：" :value="account.type"></mt-cell>
		<mt-field type="number" v-model="money" label="交易金额："></mt-field>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<submit-button @submit="submit" :submitStatus="submitStatus" value="提交"></submit-button>
		</div>
	</div>
</template>

<script>

import * as cservice from '@/services/public';
import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			account: {
				number: '',
				money: '',
				type: ''
			},
			money: '',
			submitStatus: false,
			cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
	watch: {
		money: function(val){
			if(val){
				this.submitStatus = true;
			}else{
				this.submitStatus = false;
			}
		}
	},
	methods: {
		submit() {
			service.submitLive2keepLive({
				ACCOUNT: '',
				ACCT_JGM: '',
				TRAN_AMT: '',
				ACCT_NAME: '',
				RAT: '',
				RENEW_FLAG: '',
				DEPOSIT_TERM: ''
			}).then((data) => {
				this.$router.push({name: 'LiveKeepLive2KeepLiveSuccess', params: {detail: data}});
			}, (err) => {
				console.log(err);
			});
		},
		back() {
			history.back();
		},
		getCommonAccountDetail(account) {
			cservice.getCommonAccountDetail({
				I1TRACNO: account
			}).then((data) => {
				console.log(data);
			}, (err) => {
				console.log(err);
			});
		}
	},
	created() {
		this.getCommonAccountDetail(this.$route.params.account);
	}
};

</script>


