import ajax from '@/services/ajax';
import api from '@/services/api';

export function getCommonAccountList(data) {
    return ajax.postAfterLoginQueryList('012020', data, api.getCommonAccountList);
}

export function getCommonAccountDetail(data) {
    return ajax.postAfterLoginQuery( '017403', data, api.getCommonAccountDetail);
}


